@import "main";
// End Of Import

.container{

    @media (min-width: 1500px){
        max-width: 1230px;
    }
}

// Mobile Things

.loader {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    @extend .Flex;
    background-color: #000;
    z-index: 999999;
  
    img {
      animation: pulse 2s linear infinite;
    }
}

.navover {
    position: fixed;
    width: 100%;
    height: 100%;
    background-color: rgba($color-sec, 0.7);
    z-index: 9;
    transition: 0.5s;
    opacity: 0;
    visibility: hidden;
  
    &.open {
      opacity: 1;
      visibility: visible;
    }
}

.sideNav {
    position: fixed;
    top: 0;
    right: 0;
    width: 290px;
    height: 100%;
    background-color: $color-sec;
    z-index: 99;
    overflow-y: auto;
    transition: 0.5s;
    @extend .Flex;
    flex-direction: column;
    transform-origin: right;
    transform: scaleX(0);
  
    html[dir="ltr"] & {
      transform-origin: left;
      right: unset;
      left: 0;
    }
  
    &.open {
      // transform-origin: left;
      transform: scaleX(1);
  
      ul {
        opacity: 1;
  
        li {
          transform: translateX(0);
          opacity: 1;

          @include LTR{
            transform: translateX(10px);
          }
        }
      }
    }
  
    // &.origin {
    //   transform-origin: left;
  
    //   html[dir="ltr"] & {
    //     transform-origin: right;
    //   }
    // }
  
    .options {
      padding: 0 50px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: 100%;
      position: absolute;
      top: 50px;
  
      @include mob480 {
        padding: 0 40px;
        top: 30px;
      }
  
      .lang a {
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 14px;
        color: #fff;
        width: 32px;
        height: 32px;
        border-radius: 50%;
        background-color: $color-prim;
      }
  
      .close1 {
        img {
            filter: invert(90%) sepia(17%) saturate(2147%) hue-rotate(328deg) brightness(81%) contrast(81%);
        }
      }
    }
  
    & > ul {
      display: flex;
      align-items: center;
      flex-direction: column;
      opacity: 0;
      transition: 0.5s;
      transition-delay: 0.5s;
      overflow-y: auto;
      width: 100%;
      text-align: right;
      align-items: flex-start;
      padding: 0 40px;
  
      &::-webkit-scrollbar {
        width: 8px;
      }
  
      &::-webkit-scrollbar-track {
        background-color: $color-sec;
      }
  
      &::-webkit-scrollbar-thumb {
        // border-radius: 10px;
        background-color: $color-prim;
      }
  
      & > li {
        position: relative;
        padding: 7px 0;
        // margin-bottom: 20px;
        transition: 0.5s;
        transform: translateX(-10px);
  
        @include LTR {
          transform: translateX(20px);
        }
  
        opacity: 0;
  
        &:nth-child(1) {
          transition-delay: 0.2s;
        }
  
        &:nth-child(2) {
          transition-delay: 0.4s;
        }
  
        &:nth-child(3) {
          transition-delay: 0.6s;
        }
  
        &:nth-child(4) {
          transition-delay: 0.8s;
        }
  
        &:nth-child(5) {
          transition-delay: 1s;
        }
  
        &:nth-child(6) {
          transition-delay: 1.2s;
        }
  
        &:nth-child(7) {
          transition-delay: 1.4s;
        }
  
        &:nth-child(8) {
          transition-delay: 1.6s;
        }
  
        &:nth-child(9) {
          transition-delay: 1.8s;
        }
  
        &:nth-child(10) {
          transition-delay: 2s;
        }
  
        &:nth-child(11) {
          transition-delay: 2.2s;
        }
  
        a {
          width: 100%;
          display: block;
          font-size: 18px;
          color: $color-prim;
          transition: 0.5s;
        }
  
        i {
          position: absolute;
          top: 13px;
          left: 13px;
          transform: rotate(90deg);
          font-size: 9px;
          color: currentColor;
          transition: 0.5s;
  
          html[dir="ltr"] & {
            left: unset;
            right: 13px;
          }
  
          &.open {
            transform: rotate(0deg);
          }
        }
  
        .drop {
          width: 100%;
          padding: 5px 15px;
          display: none;
          overflow: visible;
          height: auto;
  
          li {
            padding: 4px 0;
  
            a {
              font-size: 15px;
              color: #898989;
              transition: 0.5s;
  
              &:hover {
                color: #595959;
              }
            }
          }
        }
      }
    }
}

