// Start Navabr

.navBar {
    // position: fixed;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    padding: 30px 0;
    z-index: 9;

    .content{
        display: flex;
        align-items: center;

        .logo{
            margin-left: auto;
            @include LTR{
                margin-right: auto;
                margin-left: unset;
            }

            img{

                @include Sm{
                    width: 85px;
                }
            }
        }

        ul.res{
            display: flex;
            align-items: center;
            position: absolute;
            top: 50%;
            transform: translate(-50%, -50%);
            left: 50%;

            li{
                &:not(:last-child){
                    margin-left: 33px;
                    @include LTR{
                        margin-right: 33px;
                        margin-left: 0;
                    }
                }

                a{
                    font-size: 1.6rem;
                    font-family: med;
                    color: #fff;
                    @extend .trans;

                    &:hover{
                        color: $color-prim;
                    }
                }
            }

            @include Sm{
                display: none;
            }
        }

        .options {
            display: flex;
            align-items: center;

            .language{
                margin-left: 10px;
                @include LTR{
                    margin-right: 10px;
                    margin-left: 0;
                }
                width: 50px;
                height: 50px;
                border-radius: 50%;
                display: inline-flex;
                @extend .Flex;
                border: 1px solid $color-prim;
                text-transform: uppercase;
                font-family: bold;
                @extend .trans;
                color: $color-prim;

                @include Sm{
                    display: none;
                }

                &:hover{
                    background-color: $color-prim;
                    color: #fff;
                }
            }

            .volun{
                width: 140px;
                height: 50px;
                border-radius: 100px;
                background-color: $color-prim;
                border: 1px solid $color-prim;
                display: inline-flex;
                align-items: center;
                justify-content: center;
                @extend .trans;

                @include Xs{
                    width: 120px;
                    height: 40px;
                    margin-left: 20px;

                    @include LTR{
                        margin-right: 20px;
                        margin-left: 0;
                    }
                }

                svg path{
                    @extend .trans;
                }

                span{
                    color: #fff;
                    margin-right: 10px;
                    @include LTR{
                        margin-right: 0;
                        margin-left: 10px;
                    }
                    @extend .trans;
                }

                &:hover{
                    background-color: transparent;

                    span{
                        color: $color-prim;
                    }

                    svg path{
                        fill: $color-prim;
                    }
                }
            }
        }

        .menuTrigger{
            display: none;

            @include Sm{
                display: block;
            }

            img{
                filter: invert(90%) sepia(17%) saturate(2147%) hue-rotate(328deg) brightness(81%) contrast(81%);
                transform: scale(1.2);
            }
        }
    }
}

// Start Navabr

.arrows_container{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 3;

    @include Xs{
        display: none;
    }

    div[class^="swiper-button"]{
        outline: none;
        width: 50px;
        height: 50px;
        &:after, &:before{
            display: none;
        }
    }

    .swiper-button-next{
        transform: translateX(100px);
        // @include LTR{
        //     transform: translateX(100px) scaleX(-1);
        // }
    }

    .swiper-button-prev{
        transform: translateX(-100px);
        // @include LTR{
        //     transform: translateX(-100px) scaleX(-1);
        // }
    }
}

// Header slider

.header_slider{
    // background-image: url("../images/pattern.png");
    background-size: cover;
    position: relative;

    .swiper-container{
        height: 100vh;
        border-radius: 0 0 540px 540px;
        overflow: hidden;
        
        @include Xs{
            height: 70vh;
            border-radius: 0 0 100px 100px;
        }
    }

    .swiper-slide{
        height: 100%;
        @extend .Flex;
        position: relative;

        > img{

            @include Xs{
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }

        &:after{
            content: "";
            width: 100%;
            height: 100%;
            display: block;
            background: linear-gradient(#000 0%, rgba(0, 0, 0, 0) 100%);
            position: absolute;
            top: 0;
            left: 0;
            z-index: 1;
        }

        .content_s{
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            z-index: 2;
            max-width: 880px;
            text-align: center;

            @include Xs{
                max-width: 100%;
                width: 100%;
                padding: 0 15px;
            }

            h2{
                margin-bottom: 42px;
                font-size: 5rem;
                font-family: med;
                color: $color-prim;

                @include Xs{
                    font-size: 4rem;
                }
            }

            p{
                font-size: 1.8rem;
                line-height: 30px;
                color: #fff;
                margin-bottom: 50px;
            }

            a{
                width: 150px;
                height: 50px;
                border-radius: 100px;
                background-color: $color-prim;
                border: 1px solid $color-prim;
                display: flex;
                align-items: center;
                justify-content: center;
                margin: auto;
                @extend .trans;

                svg{
                    margin-left: 5px;
                    @include LTR{
                        margin-right: 5px;
                        margin-left: 0;
                        transform: scaleX(-1);
                    }

                    path{
                        @extend .trans;
                    }
                }

                span{
                    font-size: 1.6rem;
                    color: #fff;
                    font-family: bold;
                    @extend .trans;
                }

                &:hover{
                    background-color: transparent;

                    span{
                        color: $color-prim;
                    }

                    svg path{
                        fill: $color-prim;
                    }
                }
            }
        }
    }

    .swiper-pagination{
        bottom: 60px;

        span{
            width: 15px;
            height: 15px;
            background-color: rgba(#fff, .23);
            position: relative;
            outline: none;

            &:after{
                content: "";
                width: 15px;
                height: 15px;
                display: block;
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-48%, -52%);
                border: 1px solid transparent;
                border-radius: 50%;
                transition: .3s;
            }

            &.swiper-pagination-bullet-active{
                background-color: $color-prim;
                
                &:after{
                    border-color: $color-prim;
                    width: 22px;
                    height: 22px;
                }
            }
        }
    }
}

