// Volunteerings

.volunteerings{
    padding: 90px 0;

    @include Sm{
        padding: 40px 0;
    }

    .slider{
        position: relative;
    }

    .box{
        border: 1px solid rgba(#C0953F, .2);
        border-radius: 40px;
        background-color: #262626;
        // width: 295px;
        height: 295px;
        position: relative;
        overflow: hidden;
        @extend .trans;

        > svg{
            position: absolute;
