        h3{
            font-size: 2.2rem;
            font-family: bold;
            color: #fff;
            margin-top: 35px;
            text-align: center;
            padding: 0 10px;
        }

        p{
            font-size: 1.4rem;
            color: rgba(#fff, .5);
            text-align: center;
            margin: 0;
            margin-top: 20px;
            display: -webkit-box;
            -webkit-line-clamp: 2;
            -webkit-box-orient: vertical;
            overflow: hidden;
            padding: 0 10px;
        }
    }
}

