// Gallery

.gallery{
    padding: 90px 0;

    @include Sm{
        padding: 40px 0;
    }

    .nav.nav-tabs{
        border: 0;
        margin-bottom: 80px;
        display: flex;
        justify-content: center;
        flex-wrap: nowrap;

        @include Xs{
            padding: 0;
        }

        li{

            &:not(:last-child){
                margin-left: 16px;

                @include LTR{
                    margin-right: 16px;
                    margin-left: 0;
                    @include Xs{
                        margin-right: 10px;
                        margin-left: 0;
                    }
                }

            }

            a{
                background-color: transparent;
                border: 0;
                width: 162px;
                height: 50px;
                border: 1px solid $color-prim;
                border-radius: 50px;
                @extend .Flex;

                @include Xs{
                    width: 150px;
                }

                &.active{
                    background-color: $color-prim;
                    *{
                        filter: brightness(0) invert(1);
                    }
                }

                svg{
                    margin-left: 6px;
                    @include LTR{
                        margin-right: 6px;
                        margin-left: 0;
                    }
                }

                span{
                    font-size: 16px;
                    font-family: med;
                    color: $color-prim;
                }
            }
        }
    }

    .container{
        overflow: hidden;
    }

    .tab-pane.Vids{

        img{
            border-radius: 40px;
            width: 100%;
        }

        .row a{
            position: relative;
            display: block;
            overflow: hidden;
            border-radius: 40px;

            &:after{
                content: "";
                display: block;
                width: 100%;
                height: 100%;
                position: absolute;
                top: 0;
                left: 0;
                background-color: rgba(0, 0, 0, .5);
            }
            
            > svg{
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%) scale(2.2);
                z-index: 2;
            }
        }
    }

    .mini_thumbs{

        @include Sm{
            margin-top: 30px;
        }

        > .row{

            > .col-lg-6{
                &:nth-child(1),
                &:nth-child(2){
                    margin-bottom: 30px;
                }

                @include Xs{
                    margin-bottom: 30px;
                }
            }
        }
    }
    
    .gomore_link{
        @extend .Flex;
        max-width: 340px;
        height: 50px;
        margin: auto;
        margin-top: 80px;
        background-color: $color-sec;
        border-radius: 100px;
        border: 1px solid transparent;
        @extend .trans;
        position: relative;

        @include Sm{
            margin-top: 2px;
        }

        span{
            font-size: 16px;
            font-family: bold;
            color: #fff;
            margin-right: 10px;
            @include LTR{
                margin-left: 10px;
                margin-right: 0;
            }
            @extend .trans;
        }

        svg{
            
            @include LTR{
                transform: scaleX(-1);
            }
        }

        svg path{
            @extend .trans;
        }

        &:hover{
            border-color: $color-prim;
            background-color: transparent;

            span{
                color: $color-prim;
            }

            svg path{
                fill: $color-prim;
            }

            &:after,
            &:before{
                background-color: $color-prim;
            }
        }

        &:after,
        &:before{
            content: "";
            display: block;
            width: 1000px;
            height: 1px;
            background-color: rgba(#fff, .1);
            position: absolute;
            top: calc(50% - 0.5px);
            @extend .trans;
        }

        &:after{
            left: calc(100% + 37px);
        }

        &:before{
            right: calc(100% + 37px);
        }
    }

    a[data-fancybox]{

        @include Sm{
            display: flex;
            align-items: center;
            justify-content: center;
        }
    }
}

