ï»¿html {
    &::-webkit-scrollbar {
        width: 8px;
    }
    &::-webkit-scrollbar-track {
        background-color: $color-sec;
    }
    &::-webkit-scrollbar-thumb {
        // border-radius: 10px;
        background-color: $color-prim;
    }

    scroll-behavior: smooth;

    overflow-y: hidden;
    overflow-x: hidden;

    font-size: 62.5%; // 10px
    
    @include Xs{
        font-size: 56.25%; // 9px
    }

    scrollbar-width: thin;
    scrollbar-color: rgba($color-prim, 1) rgba($color-sec, 1);
}

body {
    background-color: #262626;
    font-family: "reg", sans-serif;
    text-align: start;
    font-size: 1.6rem;

    &.home{
        background-image: url("../images/pattern.png");
        background-attachment: fixed;
    }

    @include Xs{
        text-align: center;
    }
}

* {
    font-family: inherit;
}

input,
a,
button {
    font-family: "reg", sans-serif;

    &:focus {
        outline: none;
        text-decoration: none;
    }

    &:hover {
        text-decoration: none;
    }
}

button {
    cursor: pointer;

    &:focus,
    &:active {
        outline: none;
        box-shadow: none;
    }
}

ul.res {
    padding: 0;
    margin: 0;
    list-style: none;
}
