  z-index: 9;
  flex-direction: column;
  opacity: 0;
  visibility: hidden;
}

.gallery_multimedia a .overlay span {
  font-size: 18px;
  font-family: bold;
  color: #fff;
  margin-top: 15px;
}

.gallery_multimedia a:hover .overlay {
  opacity: 1;
  visibility: visible;
}

.gallery_multimedia .tab-pane.Vids .row a > svg {
  transform: scale(1) translate(-50%, -50%);
}

