.Flex {
    display: flex;
    align-items: center;
    justify-content: center;
}

.trans{
    transition: 0.6s cubic-bezier(0.83, 0, 0.17, 1);
}

@keyframes pulse {
    0% {
      opacity: 1;
    }
  
    50% {
      opacity: 0.6;
    }
  
    100% {
      opacity: 1;
    }
  }